.skills {
  background-color: aliceblue;
  text-align: center;

  h1 {
    padding-top: 40px;
    padding-bottom: 30px;
    color: black;
  }

  .categories {
    padding-top: 40px;
    padding-left: 40px;
    padding-right: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .category {
    flex: 1 0 200px;
    padding: 40px;
    border: 1px solid crimson;
    border-radius: 5px;
    text-align: center;
    background-color: white;
    box-shadow: 0px 4px 8px rgba(0, 0, 255, 0.1); 
  }

  .category:hover, .category:hover h2{
    background-color: crimson;
    color: white;
    border-color: white;
  }

  .category h2 {
    margin-bottom: 20px;
    color: crimson;
  }

  .category p {
    margin: 0;
  }
}
