@import "../../global.scss";

.contact {
    background-color: cornsilk;
    display: flex;
  
    .right {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  
      h2 {
        font-size: 30px;
      }
  
      form {
        width: 70%;
        height: 70%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
  
        input {
          width: 300px;
          height: 30px;
          font-size: 14px;

          @include mobile{
            width: 220px;
            height: 25px;
            
          }
        }
  
        textarea {
          width: 300px;
          height: 200px;
          font-size: 14px;

          @include mobile{
            width: 250px;
            height: 150px;
          }
        }
  
        button {
          width: 150px;
          height: 30px;
          color: white;
          background-color: crimson;
          border: none;
          border-radius: 10px;
          font-weight: 500;
          cursor: pointer;
  
          &:focus {
            outline: none;
          }
        }
  
        span {
          color: green;
        }
      }
  
      .social-icons {
        margin-top: 20px;
  
        a {
          margin-right: 10px;
  
          svg {
            font-size: 24px;
            color: black;
          }
        }
      }
    }
  }
  