@import "../../global.scss";

.about-container {
    background-color: peachpuff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh; /* This will make the container cover the entire viewport height */
    padding: 20px; /* Add padding from all sides */
    text-align: center; /* Center the text */
  }
  h3{
    text-align: left;
  }
  p{
    text-align: justify;
    @include mobile{
      text-align: center;
      padding-right: 30px;
    }
  }
  .about-content {
    max-width: 600px;
    @include mobile{
      max-width: 350px;
    }
  }
  
  .about-heading {
    @include mobile{
      margin-top: 10px;
    }
    margin-bottom: 40px;
  }
  
  .buttons {
    margin-top: 50px;
  }
  
  .resume-button, .linkedin-button {
    display: inline-block;
    padding: 10px 20px;
    margin-right: 20px;
    text-decoration: none;
    background-color: crimson;
    color: #fff;
    border-color: crimson;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  }
  
  .resume-button:hover, .linkedin-button:hover {
    color: crimson;
    background-color: #fff;
    border: 1px solid crimson;
  }
  