@import "../../global.scss";
.intro{
    background-color: white;
    display: flex;
    @include mobile {
        flex-direction: column;
        align-items: center;
    }
      
    .left{
        flex: 0.5;
        overflow: hidden;
        .imgContainer{
            width: 700px;
            height: 700px;
            background-color: skyblue;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;
            overflow: hidden;

            @include mobile {
                align-items: flex-start;
            }

            img{
                height: 100%;
                @include mobile{
                    height: 60%;
                }
            }
        }
    }
    .right{
        flex: 0.5;
        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile {
                padding-left: 0;
                align-items: center;
            }

            h1{
                font-size: 60px;
                margin: 10px;
                @include mobile{
                    font-style: 40px;
                }
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
                @include mobile{
                    font-style: 20px;
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            right: 40%;
            img{
                width: 30px;
                animation: blink 2s infinite;
            }
            @keyframes blink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}